	module.exports = {"data":{"package":{"id":"@glennsl/rebase","score":0.6111412848740585,"name":"@glennsl/rebase","version":"0.2.0","description":"A minimal base library for Reason","keywords":["base","foundation","bucklescript"],"license":"MIT","updated":"2017-12-31T01:47:44.672Z","stars":16,"type":"published","quality":0.8231449052727514,"popularity":0.04056518083495179,"maintenance":0.9999999999999999,"readme":"<h1>re:base</h1>\n<p>A minimal base library intended for the BuckleScript/Reason ecosystem. Uses Reasonable conventions and is easily consumed via npm.</p>\n<p><a href=\"https://npmjs.org/@glennsl/rebase\"><img src=\"https://img.shields.io/npm/v/@glennsl/rebase.svg\" alt=\"npm\"></a>\n<a href=\"https://travis-ci.org/glennsl/rebase\"><img src=\"https://img.shields.io/travis/glennsl/rebase/master.svg\" alt=\"Travis\"></a>\n<a href=\"https://coveralls.io/github/glennsl/rebase?branch=master\"><img src=\"https://img.shields.io/coveralls/glennsl/rebase/master.svg\" alt=\"Coveralls\"></a>\n<a href=\"\"><img src=\"https://img.shields.io/github/issues/glennsl/rebase.svg\" alt=\"Issues\"></a>\n<a href=\"\"><img src=\"https://img.shields.io/github/last-commit/glennsl/rebase.svg\" alt=\"Last Commit\"></a></p>\n<h2>Status</h2>\n<p>Mostly undocumented and untested. Prone to change without warning. Use at your own risk.</p>\n<h2>Project goals</h2>\n<ul>\n<li>Cross-platform</li>\n<li>Compile to efficient JavaScript code</li>\n<li>Compile to readable JavaScript code</li>\n</ul>\n<h2>Design principles</h2>\n<ul>\n<li>JS first</li>\n<li>No platform-specific APIs exposed</li>\n<li>Prefer piped form, e.g. <code>\"a\" |> String.concat(\"b\") == \"ab\"</code></li>\n<li>The subject of functions in type-wrapping modules should always be <code>t</code></li>\n<li>Enforece common patterns using module signatures</li>\n</ul>\n<h2>Installation</h2>\n<pre><code class=\"language-sh\">npm install --save @glennsl/rebase\n</code></pre>\n<p>Then add <code>@glennsl/rebase</code> to <code>bs-dependencies</code> in your <code>bsconfig.json</code>:</p>\n<pre><code class=\"language-js\">{\n  ...\n  \"bs-dependencies\": [\"@glennsl/rebase\"]\n}\n</code></pre>\n<h2>Documentation</h2>\n<p>For the moment, please see <a href=\"https://github.com/glennsl/rebase/blob/master/src/Rebase.rei\">Rebase.rei</a>. There's not many\ndoc comments yet, but most functions should be pretty self-explanatory.</p>\n","homepageUrl":"https://github.com/glennsl/rebase#readme","repositoryUrl":"https://github.com/glennsl/rebase","npmUrl":"https://www.npmjs.com/package/%40glennsl%2Frebase","issuesUrl":"https://github.com/glennsl/rebase/issues","slug":"packages/@glennsl/rebase"}},"pathContext":{"id":"@glennsl/rebase"}}

